<template>
    <div id="answer-block-wrapper">
        <div v-if="this.status==1" class="reply-main layout-center-column">
            <div>状态：未领取</div>
        </div>
        <div v-if="this.status==2" class="reply-main layout-center-column">
            <div>状态：已领取</div>
        </div>
        <div v-if="this.status==3" class="reply-main layout-center-column">
            <div>状态：已完成</div>
        </div>
    </div>
</template>
<script>
import EmptyBlock from './EmptyBlock'
var nowAid = 0, nowUid = 0;
export default {
    props:['status'],
    components:{
        'empty-block': EmptyBlock
    },
    data(){
        return{
            showAddComment: false,
            ansdataansdata:[],
            ansdata:[ {uid:1} ],
            content: `<p>hello world</p>`,
            editorOption: {}
        }
    },
    mounted(){
    },
    computed:{
        editor() {
            return this.$refs.myQuillEditor.quill;
        },
        isEmpty(){
            return this.ansdataansdata.length == 0;
        }
    },
    methods:{
        showCommentWin(uidval, aidval){
            nowAid = aidval;
            nowUid = uidval;
            this.showAddComment=!this.showAddComment;
        }
    }
}
</script>
<style scoped>
.layout-center-column{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#answer-block-wrapper{
    width: 100%;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    margin-bottom: 30px;
    text-align: left;
    background: white;
    border-radius: 15px;
    padding: 40px;


    box-shadow: 0px 0px 15px 3px rgba(200,200,200,0.3);
}
#answer-block-wrapper .reply-main{
    width: 100%;

}
#answer-block-wrapper .reply-main .head{
    width: 45px;
    height: 45px;
    float: left;
    border-radius: 100%;
    box-shadow: 0px 0px 10px 3px rgba(200,200,200,0.4);
}
#answer-block-wrapper .reply-main .name{
    width: 100%;
    font-size: 14px;
    line-height: 25px;
    font-weight: 600;
    /* background: lightcoral; */
}
#answer-block-wrapper .reply-main .content{
    width: 100%;
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 20px;
    /* background: lightblue; */
}

#answer-block-wrapper .reply-main .comment{
    width: 100%;
    margin-top: 25px;
}
</style>


