<template>
    <div class="concrete-wrapper">
        <el-container class="container">
            <el-header class="header">
                <toper></toper>
            </el-header>
            <el-main class="scroll-view" style="padding:0;">
                <div class="container-main">
                    <div class="left"><problem-block :title="questionInfo.title" :context="questionInfo.context"></problem-block></div>
                    <!-- question=问题 artical=文章-->
                    <div class="right"><problem-info v-bind:data="questionInfo" type="question"></problem-info></div>
                    <div class="operate right">
                        <el-button v-if="questionInfo.status==1" style="margin-top:20px;" type="success" @click="acceptTask()" round>&emsp;领取该任务&emsp;</el-button>
                    </div>
                    <div class="left"><answer-block v-bind:status="questionInfo.status"></answer-block></div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>
<script>
import Toper from './Toper'
import ProblemInfo from './ProblemInfo'
import ProblemBlock from './ProblemBlock.vue'
import AnswerBlock from './AnswerBlock.vue'
export default {
    components:{
        'toper': Toper,
        'problem-block': ProblemBlock,
        'problem-info':ProblemInfo,
        'answer-block':AnswerBlock
    },
    data(){
        return {
            showAnswerComment: false,
            content:'',
            editorOption:{},
            questionInfo:{
                username: '',
                hot: 0,
                time: '',
                title:'',
                context:'',
                status:1,
                tid: this.$route.query.tid
            }
        }
    },
    mounted(){
        let that = this;
        that.getQuestion();
    },
    methods:{
        //通过un获取user
        getUserById(){
            this.$postReqire(this, '/user/finduserbyname', 
                { 'username':'ss3295286' },
                (response) =>{
                    if(response.data['ERROR'] == 0){
                        
                    }else{ that.$createMessage('查找用户失败', 'error'); }
                },
                (error) => {
                    that.$createMessage('请检查网络连接', 'error');
                })
        },
        

        addHot(){
            this.$postReqire(this, '/task/readcountplus', 
                { 'task_id': this.$route.query.tid, 'read_count':this.questionInfo.hot },
                (response) =>{
                   
                },
                (error) => {
                }, true);
        },
        acceptTask(){
            if(this.questionInfo.status == 2){
                this.$createMessage('该任务已被领取', 'warning');
            }else if(this.questionInfo.status == 3){
                this.$createMessage('该任务已被完成', 'warning');
            }else{
                this.$postReqire(this, '/task/accept', 
                    { 'task_id': this.$route.query.tid, 'accept_uid':this.$store.state.user.uid },
                    (response) =>{
                        if(response.data['code'] == 1){
                            this.$createMessage('领取成功', 'success');
                            this.questionInfo.status = 2;
                        }
                    },
                    (error) => {
                        this.$createMessage('领取失败', 'error');
                    }, true);
            }
            
        },
        //获取问题
        getQuestion(){
            let that = this;
            this.$postReqire(this, '/task/findbyid', 
                { 'task_id': this.$route.query.tid },
                (response) =>{
                    that.questionInfo.username = '用户名';
                    that.questionInfo.hot = response.data['read_count'];
                    that.questionInfo.title = response.data['title'];
                    that.questionInfo.status = response.data['status'];
                    that.questionInfo.context = response.data['context'];
                    that.addHot(this.$route.query.tid, response.data['read_count']);
                },
                (error) => {
                    that.$createMessage('请检查网络连接', 'error');
                }, true);
        }
    }
}
</script>
<style scoped>
.layout-center-column{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.layout-center-column{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.concrete-wrapper{
    position: fixed;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    background: rgb(250, 250, 250);
    /* background: lightcoral; */
}

.concrete-wrapper .container{
    width: 100%;
    /* background: lightcoral; */
}
.concrete-wrapper .container .header{
    padding: 0;
}

.concrete-wrapper .container .container-main{
    width: 1150px;
    /* height: 1000px; */
    margin: 0 auto;
    background: lightblue;
}

.concrete-wrapper .container .container-main {
    width: 1150px;
    /* height: 1000px; */
    margin: 0 auto;
    background: lightblue;
}

.concrete-wrapper .container .container-main .left{
    width: 830px;
    float: left;
}
.concrete-wrapper .container .container-main .right{
    float: right;
}
</style>


